@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.color-tray-outer-1
{
width: 340px;
height:180px;
background-color: white;
display:flex;
justify-content: center;
align-items: center;
box-shadow: 0 4px 20px #d7d7d7;
border-radius: 14px;
transition: 0.5s;
/* cursor: pointer; */
font-family: 'Poppins', sans-serif;
margin-bottom: 40px;
margin-left: 40px;
}

.color-tray-outer-1:hover
{
    transform: scale(1.1);;
}

.color-tray-inner
{
    width: 320px;
    height: 160px;
    /* background-color: cadetblue; */
    margin: 8px;
}

.color-tray
{
    width: 100%;
    height: 70%;
}

.color-theme-subject
{
    background-color: white;
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.color-title
{
    color: rgb(78, 63, 161);
    letter-spacing: 1px;
}



@media only screen and (max-width: 600px) {
    
.color-tray-outer-1
{
    transform: scale(0.7);

}

.color-tray-outer-1:hover
{
    transform: scale(0.8);;
}
}
.outer
{
  width: 100%;
  padding-top: 40px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}


@media only screen and (max-width: 600px) {
.outer
{
  flex-direction: column;
  align-items: center;
}
}

.slector-outer-wrapper
{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    font-family: 'Poppins', sans-serif;


}

nav {
    width: 100vw;
    height: 14vh;
    background-color: slategray;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.1);
    z-index: 38477;
    overflow: hidden;
   
  }

  .selctor-theme-title
  {
    color: rgb(55, 151, 201);
    text-align: center;
    font-size: 1.8rem;
    initial-letter-align: 2px;
  }

 .color-pallet-wrapper
 {
     width: 80%;
     height: 86vh;
     margin: 0 auto;
     /* background-color: darkmagenta; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
 }

 .copied-alert
 {

    width: 100%;
    text-align: center;
     position: absolute;
     font-size: 3rem;
     top: 20px;
     color: rgb(111, 197, 53);
     
 }

 .color-unit-section
 {
     width: 150px;
     height: 220px;
     margin: 0 4px;

 }
 .color-filled
 {
     width: 150px;
     height: 150px;
     background-color: orchid;
     display: flex;
     justify-content: center;
     align-items: center;
     cursor: pointer;
 }

 .color-filled span
 {
     color: white;
     letter-spacing: 2px;
     font-size: 1.2rem;
 }

 .color-filled-name
 {
     margin-top: 6px;
     text-align: center;
 }



 @media only screen and (max-width: 600px) {
  
 .slector-outer-wrapper
 {
     overflow: auto;
 } 
 
 .color-pallet-wrapper
 {
     width: 80%;
    min-height:86vh;

    margin-top: 18vh;
     /* background-color: darkmagenta; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position: relative;
 }

 }


 

 
  
  

